<template lang="en">
<div>
    <div class="my-popups">
        <div class="modal d-block ">

            <div class="modal-dialog ui-draggable" style="display: inline-block;">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h5 id="ui-id-26" class="modal-title">{{ title }}</h5><button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                        <section id="main-content" class="main-content">
                            <section class="main-content">
                                <div class="container-fluid p-0">
                                    <div class="p-0">
                                        <dynamic-form ref="dynamicForm" class="daterangecontrolHS" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                                        </dynamic-form>
                                    </div>
                                </div>
                            </section>
                        </section>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<style>
.singleselectlook .multiselect__select::before {
    z-index: 99;
}

.singleselectlook .multiselect__tags {
    padding: 8px 30px 0 8px !important;
    min-height: 36px !important;
}

.singleselectlook .multiselect__input {
    margin-bottom: 0px;
}
</style>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import {
    extend,
    ValidationObserver,
    ValidationProvider
} from 'vee-validate';
import {
    ConvertCustomFieldIntoDynamicFormObj,
    ConvertDynamicFormObjToCustomFieldObj
} from "@/assets/common/vue-common.js"
import DataService from '../../services/DataService';
extend('required', {
    validate(value) {
        console.log("checkk")
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };

    },
    message: 'The {_field_} is required'
    ///computesRequired: true
});
export default defineComponent({
    name: "Manage Departemts",
    components: {
        ValidationObserver,
        ValidationProvider
    },
    props: {
        addApiUrl:{
            type:String
        },
        emitName:{
            type:String
        },
        moduleName:{
            type:String
        },
        subModule:{
            type:String
        },
        title:{
            type:String
        },
        recordId:{
            type:Number
        }
    },

    created: function () {
        let id = this.recordId ? this.recordId : 0
        this.getForm(id)
    },
    methods: {
        getForm: function (id) {
            var ths = this;
            ths.isLoading = true;
            var url = `recordId=${id}&moduleName=${this.moduleName}&subModuleCode=${this.subModule}`;
            DataService.GetDynamicForm(url).then(function (response) {
                if (response != null && response.data != null) {
                    ths.isMapFeature = response.data.isFsmLocating;
                    // response.data.data                    
                    ths.FormSchema = ths.$refs.dynamicForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    console.log(ths.FormSchema);
                    ths.FormSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if (group.group_name == "General Information") {
                                group.visibility = false;
                                if (field.name == "user_name" || field.name == "password" || field.name == "signature") {
                                    field.visibility = false;
                                }
                            }
                            field.config.onChange = ths.OnChangeEventsforField;
                        });
                    });
                    if(ths.FormSchema.length>0){
                       ths.FormSchema[0].visibility= false
                    }
                    ths.isLoading = false;
                }
            });
        },
        OnChangeEventsforField : function(x, y, z) {
            let ths = this;
            if (x == "FILL_DEPENDENT") {
                y.config.options = [];
                var url = `id=&moduleName=${ths.moduleName}&type=${y.config.fieldCode}&search=&refId=${z}`;
                DataService.GetDDLData(url).then(function (response) {
                    if (response.data) {
                        var json = response.data;
                        if (json != null) {
                            y.config.options = response.data.DATA;
                            ths.$refs.dynamicForm.UpdateKeyValue();
                        }
                    }
                });
            } else {
                if(y.name == "payment_gateway_code" && y.value == 137){
                    ths.FormSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if(field.name == "user_name") {
                                field.visibility =  true;
                                field.label = "API Login ID"
                            }
                            if(field.name == "password") {
                                field.visibility =  true;
                                field.label = "Transaction Key"
                            }
                            if(field.name == "signature") {
                                field.visibility =  false;
                            }
                        });
                    });
                } else if (y.name == "payment_gateway_code" && y.value == 135) {
                    ths.FormSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if(field.name == "user_name") {
                                field.visibility =  true;
                                field.label = "User Name"
                            }
                            if(field.name == "password") {
                                field.visibility =  true;
                                field.label = "Password"
                            }
                            if(field.name == "signature") {
                                field.visibility =  true;
                            }
                        });
                    });
                } else {
                    ths.FormSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if(field.name == "user_name") {
                                field.visibility =  true;
                                field.label = "Secret Key"
                            }
                            if(field.name == "password") {
                                field.visibility =  true;
                                field.label = "Publishable Key"
                            }
                            if(field.name == "signature") {
                                field.visibility =  false;
                            }
                        });
                    });
                }
            }
            
        },
        async onSubmit() {
            let obj = ConvertDynamicFormObjToCustomFieldObj(this.FormSchema)
            DataService.DepartmentAdd(obj,this.addApiUrl).then((res) => {
                if(res.data.Result!=null && res.data.Result.MAIN_ID>0){
                this.ShowAlert(this.$t('SavedSuccessfully'), "success", false, this.$t("Alert"));
                this.$emit(this.emitName)
                }else{
                    this.ShowAlert(this.$t(res.data.Status), "danger", false, this.$t("Alert"));
                }
                
            })
        },
        closePopup(){
            this.$emit('close')
          
        },

    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            department: {
                name: '',
                status: {
                    name: "active",
                    id: 1
                },
                visibaleToclients: false,
                visibaleToTicting: false,
                description: '',
            },
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "Cancel",
                    onClick: this.closePopup,
                },
            ],
            FormSchema: [],
            isLoading: false
        }

    },

});
</script>
